inear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.1));\n  }\n\n/* DEMO 3 ============================== */\n\n.hover-3::after {\n    content: '';\n    width: calc(100% - 3rem);\n    height: calc(100% - 3rem);\n    border: 1px solid #fff;\n    position: absolute;\n    top: 1.5rem;\n    left: 1.5rem;\n    z-index: 90;\n    transition: all 0.3s;\n    transform: scale(1.1);\n    opacity: 0;\n    display: block;\n    opacity: 0;\n  }\n\n.hover-3-content {\n    position: absolute;\n    top: 50%;\n    left: 0;\n    width: 100%;\n    transform: translateY(-50%);\n    text-align: center;\n    z-index: 99;\n  }\n\n.hover-3-description {\n    opacity: 0;\n    transform: scale(1.3);\n    transition: all 0.3s;\n  }\n\n.hover-3 img {\n    width: 110%;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n  }\n\n.hover-3 .hover-overlay {\n    background: rgba(0, 0, 0, 0.2);\n  }\n\n.hover-3:hover img {\n    width: 100%;\n  }\n\n.hover-3:hover::after {\n    opacity: 1;\n    transform: none;\n  }\n\n.hover-3:hover .hover-3-description {\n    opacity: 1;\n    transform: none;\n  }\n\n.hover-3:hover .hover-overlay {\n    background: rgba(0, 0, 0, 0.8);\n  }\n\n/* DEMO 4 ============================== */\n\n.hover-4 img {\n    width: 110%;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n  }\n\n.hover-4 .hover-overlay {\n    background: rgba(0, 0, 0, 0.4);\n    z-index: 90;\n  }\n\n.hover-4-title {\n    position: absolute;\n    bottom: 0;\n    right: 0;\n    padding: 3rem;\n    z-index: 99;\n  }\n\n.hover-4-description {\n    position: absolute;\n    top: 2rem;\n    left: 2rem;\n    text-align: right;\n    border-right: 3px solid #fff;\n    padding: 0 1rem;\n    z-index: 99;\n    transform: translateX(-1.5rem);\n    opacity: 0;\n    transition: all 0.3s;\n  }\n\n@media (min-width: 992px) {\n    .hover-4-description {\n      width: 50%;\n    }\n  }\n\n.hover-4:hover img {\n    width: 100%;\n  }\n\n.hover-4:hover::after {\n    opacity: 1;\n